* {
  padding: 0;
  margin: 0;
  box-sizing: border-box;
  text-transform: capitalize;
  font-family: "Gill Sans", "Gill Sans MT", Calibri, "Trebuchet MS", sans-serif;
}
body {
  margin: 0;
  height: 100vh;
}

header {
  display: flex;
  padding: 1em;
  justify-content: space-around;
  align-items: center;
  color: white;
  background-color: black;
}
.caraousel {
  display: grid;
  grid-template-columns: repeat(2, 1fr);
  grid-template-areas:
    "img1  img1"
    "img2  img3";
}
.caraousel > div:nth-child(1) {
  grid-area: img1;
}

.caraousel > div:nth-child(2) {
  grid-area: img2;
}
.caraousel > div:nth-child(3) {
  grid-area: img3;
}
img {
  width: 100%;
  object-fit: cover;
  padding: 0;
  margin: 0;
}

nav ul {
  display: flex;
  list-style: none;
  gap: 1em;
}
nav ul a {
  text-decoration: none;
  color: white;
  font-weight: 400;
  font-size: 1.1rem;
}
.info {
  padding: 1em;
  display: grid;
  grid-template-areas:
    "big1 small2"
    "big1 small1"
    "big2   small3"
    "big2  small4";

  place-content: center;
  width: 100%;
  margin: 0 auto;
}
.info {
  gap: 1em;
  background-image: linear-gradient(90deg, red 50%, rgba(0, 0, 0, 0.8) 50%);
  color: white;
  background-blend-mode: overlay;
}
p {
  font-size: 1.1rem;
  line-height: 1.5;
  padding: 0.5rem;
  text-align: justify;
}

.small1 {
  grid-area: small1;
}
.small2 {
  grid-area: small2;
}
.small3 {
  grid-area: small3;
}
.small4 {
  grid-area: small4;
}
.big1 {
  grid-area: big1;
}
.big2 {
  grid-area: big2;
}
nav ul li a:hover,
h1:hover {
  color: red;
}
.images {
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(300px, 1fr));
}
img {
  width: 100%;
  height: 100%;
  object-fit: cover;
  transition: 0.5s;
}
.images img:hover {
  transform: scale(0.967);
}
